<template>
  <div class="sample-view">
    <el-card>
      <div slot="header">
        <span class="card-name">查看变量</span>
      </div>
      <div class="frame-fun">
        <span class="total-samples">共 {{total}} 个变量</span>
        <el-input placeholder="请输入变量名称" prefix-icon="el-icon-search" v-model="vbname">
          <el-button slot="append" @click.stop="filterVarList" type="primary">搜索</el-button>
        </el-input>
      </div>
      <el-table :data="curVarList" stripe style="width: 100%" :header-row-style="headerStyle">
        <el-table-column type="index" label="序号" width="50" align="center"></el-table-column>
        <el-table-column prop="vbname" label="名称" align="center"></el-table-column>
        <el-table-column prop="key" label="变量标签" align="center"></el-table-column>
        <el-table-column prop="fieldtype" label="变量类别" align="center" :formatter="formatter"></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
import {getVarList} from '@/api/sample.js';

export default {
  name: 'page-views',
  data() {
    return {
      total: '',
      vbname: '',
      headerStyle: {
        color: '#409eff'
      },
      varList: [],
      curVarList: []
    };
  },
  created() {
    this.moduleName = this.$route.params.id;
    this.getVariableList(this.moduleName, this.vbname);
  },
  mounted() {},
  methods: {
    formatter(row) {
      return this.$options.filters.fieldtype(row.fieldtype);
    },
    getVariableList(moduleName = '') {
      getVarList(moduleName)
        .then(result => {
          if (Array.isArray(result.result)) {
            this.curVarList = this.varList = result.result;
            this.total = this.curVarList.length;
            return;
          }
          this.varList = [];
          this.total = 0;
        })
        .catch(err => console.log(err));
    },
    filterVarList() {
      this.curVarList = this.varList.filter(item => item.vbname.indexOf(this.vbname) > -1);
    }
  }
};
</script>

<style scoped>
.el-card {
  margin: 20px 15px;
}
.frame-fun {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.el-input {
  width: 300px;
}
.el-table {
  margin-top: 20px;
}
</style>
